.stick-hackaton {
    font-size: 13em;
    width: 0.35em;
    height: 0.35em;
    margin-bottom: 20px;
    border: 0.07em solid #000;
    position: relative;
    border-radius: 50%;
    /* z-index: 3; */
    box-shadow: 0 5px 10px black;
    &.hovered::before,
    &.hovered::after {
        opacity: 1; } }
.stick-hackaton:before {
    content: "";
    width: 0.2em;
    height: 0.2em;
    position: absolute;
    right: -0.22em;
    bottom: -0em;
    background: black;
    box-shadow: 0 5px 10px black;

    opacity: 0;
    transition: opacity 0.1s ease-in-out; }
.stick-hackaton:after {
    content: "";
    width: 1.2em;
    height: 0.33em;
    position: absolute;
    right: -1.3em;
    bottom: -0.06em;
    background: black;
    border-radius: 20px;
    box-shadow: 0 5px 10px black;

    opacity: 0;
    transition: opacity 0.3s ease-in-out; }
